import Artplayer from 'artplayer';
import FlvJs from 'flv.js';
import Danmaku from 'danmaku';

import ploading from "./img/ploading.gif";
import state from "./img/state.png";
import indicator from "./img/indicator.svg";

(() => {
    let hadPause = true,
        parmfu = function (item, defaul) {
            if (!item) return;
            var url = window.location.search.match(/(?![?|&]).*?(?=(&|$))/g),
                ii = new Array(2),
                io;
            for (var i = 0; i < url.length || url[i].length; i++) {
                ii = url[i].match(/([^=]+)/g);
                if (!ii || !ii[0] || io) break;
                io = ii[0] === item ? ii[1] : undefined;
            }
            return io ? io : defaul;
        },
        player = new Artplayer({
            container: '.artplayer-app',
            url: "/stream?_=" + new Date().getTime(),
            title: "" + new Date().getTime(),
            type: parmfu("type","mp4"),
            volume: 0.5,
            isLive: true,
            muted: false,
            autoplay: true,
            pip: true,
            autoSize: true,
            autoMini: true,
            screenshot: true,
            setting: true,
            loop: false,
            flip: true,
            playbackRate: true,
            aspectRatio: true,
            fullscreen: true,
            fullscreenWeb: true,
            subtitleOffset: true,
            miniProgressBar: true,
            mutex: true,
            backdrop: true,
            playsInline: true,
            autoPlayback: true,
            theme: '#23ade5',
            lang: navigator.language.toLowerCase(),
            whitelist: ['*'],
            moreVideoAttr: {
                crossOrigin: 'anonymous',
            },
            settings: [],
            contextmenu: [],
            layers: [],
            quality: [],
            thumbnails: {},
            subtitle: {},
            highlight: [],
            controls: [],
            icons: {
                loading: '<img src=' + ploading + '>',
                state: '<img width="150" heigth="150" src=' + state + '>',
                indicator: '<img width="16" heigth="16" src=' + indicator + '>',
            },
            customType: {
                flv: function (video, url) {
                    if (FlvJs.isSupported()) {
                        const flvPlayer = FlvJs.createPlayer({
                            type: 'flv',
                            url: url,
                        });
                        flvPlayer.attachMediaElement(video);
                        flvPlayer.load();
                    } else {
                        art.notice.show = 'flv';
                    }
                },
            },
        });
    player.on('play', (...args) => {
        if (hadPause) player.switchUrl("/stream?_=" + new Date().getTime(), new Date().getTime());
        hadPause = false;
    });
    player.on('pause', (...args) => {
        hadPause = true;
    });
    /**
     * ws 
     */
    function ws() {
        if (window["WebSocket"]) {
            let danmaku = new Danmaku({
                container: document.getElementsByClassName('art-danmuku')[0],
            });
            setInterval(function () { danmaku.resize() }, 1000)
            var conn = new WebSocket("ws://" + window.location.host + "/ws");
            conn.onopen = function () {
                setInterval(function () {
                    conn.send("test")
                }, 1000)
            }
            conn.onclose = function () {
                ws()
            };
            conn.onmessage = function (evt) {
                try {
                    let data = JSON.parse(evt.data)
                    danmaku.emit(data)
                } catch (e) {
                    console.log(e)
                    console.log(evt.data)
                }
            };
        }
    } ws();
})()
